<template>
  <div>
    1.绑定class和style :class="{ classNameA: trueValue,classNameB:trueValue}" :style="{color:colorValue}"
    <br />2.v-for遍历对象 v-for="(value, name, index) in object" value:value值 name:key关键值 index:序号; 不要使用对象或数组之类的非基本类型值作为 v-for 的 key,用字符串或数值类型的值。
    <br />3.事件处理(1)阻止单击事件继续传播v-on:click.stop=""
    <br />(2)提交事件不再重载页面 form表单 v-on:submit.prevent="onSubmit"
    <br />(3)修饰符可以串联 v-on:click.stop.prevent=""
    <br />(4)添加事件监听器时使用事件捕获模式,即内部元素触发的事件先在此处理，然后才交由内部元素进行处理 v-on:click.capture=""
    <br />(5)只当在 event.target 是当前元素自身时触发处理函数,即事件不是从内部元素触发的 v-on:click.self="fn"
    <br />(6)v-on:click.prevent.self 会阻止所有的点击，而 v-on:click.self.prevent 只会阻止对元素自身的点击。
    <br />(7)点击事件将只会触发一次v-on:click.once=""
    <br />(8)滚动事件的默认行为 (即滚动行为) 将会立即触发,滚动过程中 v-on:scroll.passive=""
    <br />(9)按键修饰 v-on:keyup.page-down=""
    <br />(10)鼠标修饰符 .left .right .middle
    <a href="javaScript:;" @click.left.once.prevent="showFlag">鼠标点击事件</a>
    <br />(11)text 和 textarea 元素使用 value 属性和 input 事件；checkbox 和 radio 使用 checked 属性和 change 事件；select 字段将 value 作为 prop 并将 change 作为事件
    <br />
    <input type="checkbox" id="checkbox" v-model="checked" />
    <label for="checkbox">{{ checked }}</label>

    <input type="checkbox" id="jack" value="Jack" v-model="checkedNames" />
    <label for="jack">Jack</label>
    <input type="checkbox" id="john" value="John" v-model="checkedNames" />
    <label for="john">John</label>
    <input type="checkbox" id="mike" value="Mike" v-model="checkedNames" />
    <label for="mike">Mike</label>
    <span>Checked names: {{ checkedNames }}</span>
    <input type="radio" value="One" id="one" v-model="picked" />
    <label for="one">One</label>
    <input type="radio" value="Two" id="two" v-model="picked" />
    <label for="two">Two</label>
    <span>Picked: {{ picked }}</span>

    <!-- 如果多选 multiple 绑定值为数组 -->
    <select v-model="selected">
      <option>A</option>
      <option>B</option>
      <option>C</option>
    </select>
    <span>Selected: {{ selected }}</span>

    <select v-model="selectedObj">
      <!-- 内联对象字面量 -->
      <option v-bind:value="{ number:'aaa' }">123</option>
      <option v-bind:value="{ string:'bbb'  }">456</option>
    </select>
    <span>Selected: {{ selectedObj }}</span>
    <br />12.v-model 修饰符 .lazy输入完成后再赋值 .number .trim去除首尾空白
    <input v-model.lazy.number="inputValue" />
    <br />13.组件传值 子组件两种传值方式 如下pushValue pullValue
    <!-- v-on 来监听$emit 提交的事件 获取传递的值-->
    <iSymbol
      style="display:inline-block"
      :iconName="iconName"
      iconColor="red"
      :iconWidth="25"
      v-slot:msg
      :iconHeight="25"
      @pullValue="pullValue"
      @pushValue="pushValue($event)"
    ></iSymbol>
    <br />v-on事件监听器在DOM模板中会被自动转换为全小写(因为HTML是大小写不敏感的)v-on:myEvent将会变成v-on:myevent——导致myEvent不可能被监听到,推荐你始终使用 kebab-case 的事件名
    <br />
    Vue项目完成后，需要打包上线。打包只需要执行命令npm run build即可，但有两点需要注意的地方，否则就会出现空白页或图片不显示问题。
    ① 修改路径位置，解决空白页问题。
    修改位于项目目录中的config/index.js，将assetsPublicPath: '/'修改为assetsPublicPath: './'，注意有两处。如下：
    这样添加一个点就将路径变成了相对路径。
    ②解决背景图片不显示问题。
    打包后的css文件夹内app.css文件访问static/img/’图片名’，路径错误访问不到图片。
    修改位于项目目录中的build文件夹/utils.js，在如下图所示位置添加上publicPath:'../../'：
  </div>
</template>
<script>
import tools from "../../utils/tools";
export default {
  name: "VueBaseKnowledage",
  data() {
    return {
      checked: true,
      checkedNames: [],
      picked: "one",
      selected: "B",
      iconName: "personManage",
      selectedObj: "456",
      inputValue: "",
      msg: "测试"
    };
  },
  created() {
  },
  methods: {
    showFlag: () => {
      console.log("test");
    },
    switchBtn: function(val) {
      // console.log(val);
      // console.log(this.$refs.jack1.value);
    },
    pullValue: function(val) {
      //   if (val === "persons") {
      //     this.iconName = "personManage";
      //   } else {
      //     this.iconName = "persons";
      //   }
    },
    pushValue: function(val) {
      if (val === "persons") {
        this.iconName = "personManage";
      } else {
        this.iconName = "persons";
      }
      // console.log(val);
    }
  },
  watch: {
    inputValue: function(val) {
      this.inputValue = tools.checkNumber(val, 2);
    }
  }
};
</script>
<style lang="scss">
</style>



